.navbar{
    display: flex;
    justify-content: space-around;
    padding:  8px;
    padding-top: 12px;
    box-shadow: 0 1px  3px -2px black;

}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: 60px;
}
.nav-logo img{
    width: 140px;
    height: 60px;
    margin-left: -30px;
    padding-right: 60px;
}
.nav-logo p{
    color: #171717;
    font-size: 25px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 18px;
    font-weight: 500;
}
.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background:red;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
    padding-left: 60px;
    
}
.nav-login-cart button{
    width: 95px;
    height: 38px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 15px;
    font-weight: 500;
    background: white;
    cursor: pointer;

}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -33px;
    margin-left: -50px;
    border-radius: 18px;
    font-size: 14px;
    background: red;
    color: white;
}