.descriptionbox{
    margin: 80px 120px;

}
.descriptionbox-navigator{
        display: flex;
}
.descriptionbox-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-weight: 600;
    width: 151px;
    height: 60px;
    border: 1px solid #d0d0d0;
}
.descriptionbox-nav-box.fade{
    background: #fbfbfb;
    color: #555;

}
.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border: 1px solid #d0d0d0;
    padding: 38px;
    padding-bottom: 40px;

}