.shopcategory-banner{
    display: block;
    margin: 10px auto;
    width: 95%;
}
.shopcategory-indexSort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;

}
.shopcategory-indexSort p span{
    font-weight: 600;
}
.shopcategory-products{
    margin: 30px 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 80px;
}
.shopcategory-products img {
    height: 250px;
    width: 300px;


}
.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    width: 180px;
    height: 50px;
    border-radius: 75px;
    background:#ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}